.game {
    display: flex;
    flex-direction: column;
    font-family: Lato;


    &__board {
        box-sizing: border-box;
        height: 400px;
        background-color: #3d7fba;
        display: grid;
        grid-template-columns: 0.8fr 0.8fr 0.8fr;
        grid-template-rows: 1fr 1fr 1fr;
        grid-column-gap: 10px;
        grid-row-gap: 10px;
        pointer-events: auto;
        margin: 10px auto;




        &__square {
            background-color: #ffffff;
            color: #000000;
            padding: 10px;
            font-size: 20px;
            cursor: pointer;
            width: 100px;
        }



    }

    &__title {
        font-size: 50px;
        color: white;
        width: 100%;
        /* font-weight: bold; */
        margin: 0px auto 20px;
        background: #3d7fba;
        text-align: center;
        padding: 10px;
    }

    &__reset {
        width: 100px;
        margin: 10px auto;
        background: #3d7fba;
        color: white;
        height: 50px;
        font-size: 16px;
    }

    &__turn {
        font-size: 30px;
        color: black;
        margin: 10px auto 20px;
    }
}